n\r\n        th::before {\r\n          content: \"\\00a0\";\r\n        }\r\n      }\r\n    }\r\n    tbody {\r\n      display: block;\r\n      width: auto;\r\n      position: relative;\r\n      overflow-x: auto;\r\n      white-space: nowrap;\r\n\r\n      tr {\r\n        display: inline-block;\r\n        vertical-align: top;\r\n      }\r\n    }\r\n    th {\r\n      display: block;\r\n      text-align: right;\r\n    }\r\n    td {\r\n      display: block;\r\n      min-height: 1.25em;\r\n      text-align: left;\r\n    }\r\n    tr { padding: 0 10px; }\r\n\r\n    /* sort out borders */\r\n    thead {\r\n      border: 0;\r\n      border-right: 1px solid $table-border-color;\r\n    }\r\n\r\n    &.bordered {\r\n      th { border-bottom: 0; border-left: 0; }\r\n      td { border-left: 0; border-right: 0; border-bottom: 0; }\r\n      tr { border: 0; }\r\n      tbody tr { border-right: 1px solid $table-border-color; }\r\n    }\r\n\r\n  }\r\n\r\n}\r\n\r\n\r\n// Collections\r\n.collection {\r\n  margin: $element-top-margin 0 $element-bottom-margin 0;\r\n  border: 1px solid $collection-border-color;\r\n  border-radius: 2px;\r\n  overflow: hidden;\r\n  position: relative;\r\n\r\n  .collection-item {\r\n    background-color: $collection-bg-color;\r\n    line-height: $collection-line-height;\r\n    padding: 10px 20px;\r\n    margin: 0;\r\n    border-bottom: 1px solid $collection-border-color;\r\n\r\n    // Avatar Collection\r\n    &.avatar {\r\n      min-height: 84px;\r\n      padding-left: 72px;\r\n      position: relative;\r\n\r\n      // Don't style circles inside preloader classes.\r\n      &:not(.circle-clipper) > .circle,\r\n      :not(.circle-clipper) > .circle {\r\n        position: absolute;\r\n        width: 42px;\r\n        height: 42px;\r\n        overflow: hidden;\r\n        left: 15px;\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n      }\r\n      i.circle {\r\n        font-size: 18px;\r\n        line-height: 42px;\r\n        color: #fff;\r\n        background-color: #999;\r\n        text-align: center;\r\n      }\r\n\r\n\r\n      .title {\r\n        font-size: 16px;\r\n      }\r\n\r\n      p {\r\n        margin: 0;\r\n      }\r\n\r\n      .secondary-content {\r\n        position: absolute;\r\n        top: 16px;\r\n        right: 16px;\r\n      }\r\n\r\n    }\r\n\r\n\r\n    &:last-child {\r\n      border-bottom: none;\r\n    }\r\n\r\n    &.active {\r\n      background-color: $collection-active-bg-color;\r\n      color: $collection-active-color;\r\n\r\n      .secondary-content {\r\n        color: #fff;\r\n      }\r\n    }\r\n  }\r\n  a.collection-item{\r\n    display: block;\r\n    transition: .25s;\r\n    color: $collection-link-color;\r\n    &:not(.active) {\r\n      &:hover {\r\n        background-color: $collection-hover-bg-color;\r\n      }\r\n    }\r\n  }\r\n\r\n  &.with-header {\r\n    .collection-header {\r\n      background-color: $collection-bg-color;\r\n      border-bottom: 1px solid $collection-border-color;\r\n      padding: 10px 20px;\r\n    }\r\n    .collection-item {\r\n      padding-left: 30px;\r\n    }\r\n    .collection-item.avatar {\r\n      padding-left: 72px;\r\n    }\r\n  }\r\n\r\n}\r\n// Made less specific to allow easier overriding\r\n.secondary-content {\r\n  float: right;\r\n  color: $secondary-color;\r\n}\r\n.collapsible .collection {\r\n  margin: 0;\r\n  border: none;\r\n}\r\n\r\n\r\n\r\n// Responsive Videos\r\n.video-container {\r\n    position: relative;\r\n    padding-bottom: 56.25%;\r\n    height: 0;\r\n    overflow: hidden;\r\n\r\n    iframe, object, embed {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n}\r\n\r\n// Progress Bar\r\n.progress {\r\n    position: relative;\r\n    height: 4px;\r\n    display: block;\r\n    width: 100%;\r\n    background-color: lighten($progress-bar-color, 40%);\r\n    border-radius: 2px;\r\n    margin: $element-top-margin 0 $element-bottom-margin 0;\r\n    overflow: hidde